<template>
  <div class="sidebar-user-stories">
    <h1 class="sidebar-user-stories__caption">Истории</h1>
    <Slider :slider-length="3" :slide-index="slideIndex">
      <li class="slider__list-item">
        <SlideUserStory :data-list="stories1" />
      </li>
      <li class="slider__list-item">
        <SlideUserStory :data-list="stories2" />
      </li>
      <li class="slider__list-item">
        <SlideUserStory :data-list="stories3" />
      </li>
    </Slider>
    <!-- Add story button -->
    <div class="sidebar-user-stories__add-story-button" @click="onAddStory">
      <div class="elem-register"></div>
      ДОБАВИТЬ СВОЮ ИСТОРИЮ
    </div>
  </div>
</template>

<script>
import Slider from '@/components/Slider.vue'
import SlideUserStory from '@/components/SlideUserStory.vue'

const slideTime = 20        // seconds
let intervalId = null

const stories = generateRandomStories()

export default {
  name: 'SidebarUserStories',
  components: {
    Slider,
    SlideUserStory
  },
  data () {
    return {
      stories1: stories.splice(0, 3),
      stories2: stories.splice(0, 3),
      stories3: stories.splice(0, 3),
      slideIndex: 0
    }
  },
  methods: {
    onAddStory () {
      alert('Add your story')
    }
  },
  created () {
    intervalId = setInterval(() => {
      if (this.slideIndex < 2) {
        this.slideIndex++
      } else {
        this.slideIndex = 0
      }
    }, slideTime * 1000)
  },
  beforeDestroy () {
    if (intervalId !== null) clearInterval(intervalId)
  }
}

// Заглушка для генерации рандомных 9 историй
function generateRandomStories () {
    const res = []
    for (let i = 1; i <= 9; i++) {
        res.push({
            firstName: 'ИВАН ' + i,
            lastName: 'ИВАНОВ ' + i,
            otherName: 'ИВАНОВИЧ ' + i,
            rank: 'ЛЕЙТЕНАНТ ' + i
        })
    }
    return res
}
</script>

<style lang="scss">
.sidebar-user-stories {
  width: 440px;
  height: 390px;
  margin-top: 15px;
  padding-top: 16px;
  position: relative;
  background-image: url('../assets/img/ir/bg-440.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  &__caption {
    font-family: 'B52';
    font-size: 36px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: -1.35px;
    text-align: center;
    color: #5b4746;
    margin-bottom: 10px;
  }
  &__add-story-button {
    position: relative;
    display: block;
    width: 194px;
    height: 30px;
    margin: 0 auto;
    font-size: 12px;
    color: #fff;
    opacity: 0.95;
    background-image: url('../assets/img/s-default.png');
    text-align: center;
    line-height: 30px;
    &:hover {
      cursor: pointer;
      opacity: 1;
    }
    .elem-register {
      position: absolute;
      top: -4px;
      left: -24px;
      width: 40px;
      height: 46px;
      background-image: url('../assets/img/elemet-register.png');
    }
  }
}
</style>
