<template>
<div class="slider">
    <div class="slider__left-btn" :class="{ disabled: slideInd <= 0 }" @click="slideLeft"></div>
    <div class="slider__right-btn" :class="{ disabled: slideInd >= sliderLength - 1 }" @click="slideRight"></div>
    <div class="slider__body">
        <ul class="slider__list" :style="{ left: -1 * slideInd * 466 + 'px' }">
            <slot />
        </ul>
    </div>
</div>
</template>

<script>
export default {
    name: 'Slider',
    props: {
        sliderLength: {
            type: Number,
            required: true
        },
        slideIndex: {
            type: Number,
            default: 0
        }
    },
    data () {
        return {
            slideInd: this.slideIndex
        }
    },
    methods: {
        slideLeft () {
            if (this.slideInd > 0) this.slideInd--
            // else this.slideIndex = this.sliderLength - 1
        },
        slideRight () {
            if (this.slideInd < this.sliderLength - 1) this.slideInd++
            // else this.slideIndex = 0
        }
    },
    watch: {
        slideIndex (val) {
            this.slideInd = val
        }
    }
}
</script>

<style lang="scss">
.slider {
    position: relative;
    width: 440px;
    &__body {
        position: relative;
        width: 366px;
        margin: 0 auto;
        overflow-x: hidden;
    }
    &__list {
        display: inline-block;
        position: relative;
        top: 0;
        width: 3000px;
        -webkit-transition: left 1s ease-out;
        -moz-transition: left 1s ease-out;
        -o-transition: left 1s ease-out;
        transition: left 1s ease-out;
    }
    &__list-item {
        display: inline-block;
        width: 366px;
        margin-right: 100px;
        vertical-align: top;
    }
    &__left-btn, &__right-btn {
        position: absolute;
        top: 100px;
        width: 24px;
        height: 24px;
        &:hover {
            cursor: pointer;
        }
    }
    &__left-btn {
        background: url('../assets/img/ir/24-arrow-preview-default.png') 100% 100% no-repeat;
        left: 12px;
        &.disabled {
            background: url('../assets/img/ir/24-arrow-preview-dissabled.png') 100% 100% no-repeat;
            &:hover {
                cursor: default;
            }
        }
    }
    &__right-btn {
        background: url('../assets/img/ir/24-arrow-next-default.png') 100% 100% no-repeat;
        right: 12px;
        &.disabled {
            background: url('../assets/img/ir/24-arrow-next-dissabled.png') 100% 100% no-repeat;
            &:hover {
                cursor: default;
            }
        }
    }
}
</style>
