<template>
  <div class="sidebar-stat">
    <Slider :slider-length="4">
      <li class="slider__list-item">
        <SlideRating :data-list="ratesCity" />
      </li>
      <li class="slider__list-item">
        <SlideRating :data-list="ratesRegion" />
      </li>
      <li class="slider__list-item">
        <SlideTop :members="1638" />
      </li>
      <li class="slider__list-item">
        <SlideStatStory :data-list="statStory" />
      </li>
    </Slider>
  </div>
</template>

<script>
import Slider from '@/components/Slider.vue'
import SlideRating from '@/components/SlideRating.vue'
import SlideTop from '@/components/SlideTop.vue'
import SlideStatStory from '@/components/SlideStatStory.vue'

export default {
  name: 'SidebarStat',
  components: {
    Slider,
    SlideRating,
    SlideTop,
    SlideStatStory
  },
  data () {
    return {
      ratesCity: genRating('CITY'),
      ratesRegion: genRating('REGION'),
      statStory: genStatStory()
    }
  }
}

// Заглушка для генерации рандомной статистики
function genRating (label) {
  const res = []
  for (let i = 1; i <= 6; i++) {
    res.push({
      value: parseInt(Math.random() * 1800) + 200,
      city: label + i
    })
  }
    
  return res.sort((a, b) => b.value - a.value)
}

// Заглушка для генерации рандомной истории сохранений
function genStatStory () {
  const res = []
  for (let i = 0; i < 6; i++) {
    res.push({
      firstName: 'ИВАН ' + (i + 1),
      lastName: 'ИВАНОВ ' + (i + 1),
      otherName: 'ИВАНОВИЧ ' + (i + 1),
      storyCount: i * 100
    })
  }
    
  return res
}
</script>

<style lang="scss">
.sidebar-stat {
  width: 440px;
  height: 360px;
  margin-top: 15px;
  padding-top: 16px;
  position: relative;
  background-image: url('../assets/img/ir/bg-440.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  .slider__left-btn,
  .slider__right-btn {
    top: 150px;
  }
}
</style>
