<template>
<div class="slide-rating">
    <!-- Slide rating caption -->
    <h1 class="slide-rating__caption">Статистика</h1>
    <!-- Bar list -->
    <ul class="bar-list">
        <li
            v-for="b in 6" :key="'b' + b"
            class="bar-list__item"
            :style="{ left: (b - 1) * 60.5 + 'px', height: computedList[b - 1].percent + '%' }"
        >
            <div class="bar-list__item-value">{{ computedList[b - 1].value }}</div>
        </li>
    </ul>
    <!-- Label list -->
    <ul class="label-list">
        <li
            v-for="l in 6" :key="'l' + l"
            class="label-list__item"
            :style="{ left: (l - 1) * 49 + 'px' }"
        >
            {{ computedList[l - 1].city }}
        </li>
    </ul>
</div>
</template>

<script>
export default {
    name: 'SlideRating',
    props: {
        dataList: {
            type: Array,
            required: true
        }
    },
    data () {
        return {
            barColors: ['#745F4C', '#617070', '#81687E', '#A2785A', '#A49957', '#B77B78'],
        }
    },
    computed: {
        computedList () {
            const max = this.dataList[0].value
            return this.dataList.map((elem) => {
                elem.percent = parseInt(elem.value / max * 100)
                return elem
            })
        }
    }
}
</script>

<style lang="scss">
.slide-rating {
    width: 366px;
    &__caption {
        font-family: 'B52';
        font-size: 36px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: -1.35px;
        text-align: center;
        color: #5b4746;
        margin-bottom: 10px;
    }
    .bar-list {
        display: block;
        width: 340px;
        height: 225px;
        margin-top: 28px;
        margin-left: 12px;
        background-image: url('../assets/img/ir/lines.png');
        position: relative;
        &__item {
            position: absolute;
            bottom: 0;
            width: 37px;
            &:nth-child(1) {
                background-image:url('../assets/img/ir/chart-1.png');
                background-repeat: repeat-y;
            }
            &:nth-child(2) {
                background-image:url('../assets/img/ir/chart-2.png');
                background-repeat: repeat-y;
            }
            &:nth-child(3) {
                background-image:url('../assets/img/ir/chart-3.png');
                background-repeat: repeat-y;
            }
            &:nth-child(4) {
                background-image:url('../assets/img/ir/chart-4.png');
                background-repeat: repeat-y;
            }
            &:nth-child(5) {
                background-image:url('../assets/img/ir/chart-5.png');
                background-repeat: repeat-y;
            }
            &:nth-child(6) {
                background-image:url('../assets/img/ir/chart-6.png');
                background-repeat: repeat-y;
            }
        }
        &__item-value {
            position: absolute;
            width: 38px;
            height: 28px;
            left: -2px;
            top: -28px;
            text-align: center;
            background:url('../assets/img/ir/hints.png') 100% 100% no-repeat;
            font-family: B52;
            font-size: 14.1px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: normal;
            letter-spacing: normal;
            color: #f3f3f3;
            padding-top: 2px;
        }
    }
    .label-list {
        display: block;
        margin-top: 3px;
        margin-left: 2px;
        &__item {
            display: inline-block;
            width: 60px;
            font-size: 12px;
            text-align: center;
            padding: 2px;
            font-family: RobotoSlab;
            font-size: 11px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: normal;
            letter-spacing: 0.1px;
            text-align: center;
            color: #5b4746;
            overflow: hidden;
        }
    }
}
</style>
