<template>
  <div class="select-lang-box">
    <select v-model="selectedLanguage">
      <option v-for="(lang, index) in languages" :key="index" :value="lang.short">{{ lang.short }}</option>
	</select>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import Vue from 'vue';
    
export default {
  name: 'SelectLang',
  computed: {
    ...mapState(['languages'])
  },
  data () {
    return {
      selectedLanguage: this.$store.state.curLanguage
    }
  },
  watch: {
    'selectedLanguage': function (newLang) {
      Vue.i18n.set(newLang)
      this.$store.commit('setLanguage', newLang)
      this.$cookies.set('lang', newLang)
    }
  }
}
</script>

